<template>
  <div>
    <div>
      <el-button icon="el-icon-plus" size="small" type="primary"
                 @click="showAddMap">
      </el-button>
    </div>
    <div class="mapMain">
      <el-table :data="maps" border @selection-
                change="handleSelectionChange" size="small" stripe style="width:70%">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="id" label="" width="55"></el-table-column>
        <el-table-column prop="name" label="" width="180">
        </el-table-column>
        <el-table-column prop="createDate" width="150" label="
"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button size="mini"
                       @click="showLookView(scope.$index, scope.row)">
            </el-button>
            <el-button size="mini" type="danger"
                       @click="handleDelete(scope.$index, scope.row)">
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-button @click="deleteMany" type="danger" size="small"
                 style="margin-top: 8px" :disabled="multipleSelection.length==0"> 
      </el-button>
    </div>
    <el-dialog title="" :visible.sync="dialogVisible_show"
               width="80%">
      <div class="img-container">
        <img :src="m.url" :alt="m.name" :title="m.name" class="map-
img">
      </div>
      <span slot="footer" class="dialog-footer">
<el-button size="small" @click="dialogVisible_show = false">
</el-button>
</span>
    </el-dialog>
    <el-dialog title="" :visible.sync="dialogVisible_add"
               width="40%">
      <div>
        <div>
          <el-tag></el-tag>
          <el-input class="addMapInput" size="small" vmodel="AddMap.name"></el-input>
        </div>
        <div>
          <el-tag> url</el-tag>
          <el-input class="addMapInput" size="small" vmodel="AddMap.url"></el-input>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
<el-button size="small" @click="dialogVisible_add = false">
 </el-button>
<el-button size="small" type="primary" @click="addMap"> 
</el-button>
</span>
    </el-dialog>
  </div>
</template>
<script>
function getFileUrl(obj) {
  let url;
  url = window.URL.createObjectURL(obj.files.item(0));
  return url;
}

export default {
  name: "map",
  data() {
    return {
      m: {
        name:
            ''
        ,
        url: ''
      },
      dialogVisible_show: false,
      dialogVisible_add: false,
      loading: false,
      AddMap: {
        name:
            ''
        ,
        url: ''
      },
      multipleSelection: [],
      maps: []
    }
  },
  mounted() {
    this.initMaps();
  },
  methods: {
    deleteMany() {
      this.$confirm('          ' +
          this.multipleSelection.length + ', ?', '',
          {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
        let ids = '?';
        this.multipleSelection.forEach(item => {
          ids += 'ids=' + item.id + '&';
        })
        this.deleteRequest("/map/manage/" +
            ids).then(resp => {
          if (resp) {
            this.initMaps();
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message:
              ''
        });
      });
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    showAddMap() {
      this.AddMap.name = '';
      this.AddMap.url = '';
      this.dialogVisible_add = true;
    },
    async uploadImg() {
      const that = this;
      const inputFile = await this.$refs.fileBtn.files[0];
      let res;
      console.log('');
      console.log(inputFile);
      this.inputFile = inputFile;
      if (this.inputFile) {
        let inputFile = this.inputFile;
        if (inputFile.type !== 'image/jpeg' &&
            inputFile.type !== 'image/png' && inputFile.type !==
            'image/gif') {
          alert('');
          return;
        }
        inputFile.lastModifiedDate.toLocaleString()
        const reader = new FileReader();
        res = reader.readAsDataURL(this.inputFile);
        console.log('');
        reader.onloadend = function () {
          reader.result.substring(84);
          var strBase64 = reader.result.substring(0);
          console.log(strBase64);
        };
        reader.onload = function (e) {
          console.log(e);
          that.AddMap.url = this.result;// : this.result , this  vue , reader , that  vue 
          that.imgSrc
          that.AddMap.url = getFileUrl(that.AddMap.url);
        };
      } else {
        return;
      }
    },
    addMap() {
      this.postRequest("/map/manage/", this.AddMap).then(resp => {
        if (resp) {
          this.initMaps();
          this.dialogVisible_add = false;
        }
      })
    },
    showLookView(index, data) {
      Object.assign(this.m, data);
      this.dialogVisible_show = true;
    },
    handleDelete(index, data) {
      this.$confirm('' + data.name + ',' + '?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.deleteRequest("/map/manage/" + data.id).then(resp => {
          if (resp) {
            this.initMaps();
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message:
              ''
        });
      });
    },
    initMaps() {
      this.loading = true;
      this.getRequest("/map/manage/").then(resp => {
        this.loading = false;
        if (resp) {
          this.maps = resp;
        }
      })
    }
  }
}
</script>
<style>
.addMapInput {
  width: 200px;
  margin-left: 8px;
}

.mapMain {
  margin-top: 10px;
}

.map-img {
  width: 600px;
  height: 600px;
  border-radius: 72px;
}

.img-container {
  width: 900px;
  height: 900px;
  display: flex;
  justify-content: center;
}
</style>